.custom-modal-cover {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10; 
    -webkit-transform: translateZ(0); 
            transform: translateZ(0);
    background-color: rgba(34, 34, 34, 0.438);
}

.btn-dismiss {
    border: none;
}
  
.custom-modal {
    margin: 30px auto 0 auto;
    max-width: 600px;
    background-color: #FFFFFF;
    box-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.1);
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    background: linear-gradient( rgba(0, 0, 0, 0.5), #212529 ), url(/static/media/reading.f2837c0b.png);
}


.message-body {
    padding-left: 1.1rem;
    padding-right: 1.1rem;
    padding-bottom: 1rem;
}

.message-image {
    width: 80px;
}

.message-form {
    padding: 20px 0;
    margin: 0 auto;
    max-width: 480px;
    -webkit-align-content: center;
            align-content: center;
}

.send-message {
    margin-top: 10px;
    color: #dc3545;
