}

.mobile-share {
    display:none;
}


@media only screen and (max-width: 650px) {
    .custom-modal { 
        width: 95%;
    }

    .message-body {
        padding-left: 2rem;
        padding-right: 2rem;
    }

    .message-image {
        margin: 0 auto;
        -webkit-align-items: center;
                align-items: center; /* align vertical */
        margin-bottom: 10px;
    }

    .message-book {
        display: -webkit-flex;
        display: flex;
        -webkit-align-items: center;
                align-items: center; /* align vertical */
        -webkit-justify-content: center;
                justify-content: center;
        text-align: center;
    }
   
    .message-form{
        margin-top: 0;
        margin-left: 5px;
        margin-right: 10px
    } 

    .modal-title {
        font-size: 24px;
        margin-right: 10px;
    }

}

@media only screen and (max-width: 470px) {
    .message-body {
        padding-left: 3rem;
        padding-right: 3rem;
        width: 100%
    }

    .message-book {
        margin: 5px;        
    }

    .modal-title {
        font-size: 18px;
        margin-right: 10px;
    }

    .message-image {
        width: 60px;
        height: 90px;
    }

    .message-form{
        margin-top: 0;
        margin-left: 0px;
        margin-right: 0px
    }

    .mobile-share {
        display: inherit;
    }

    .reg-share {
        display: none;
    }
}
